import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IMapKeyValue, IOptionsPsContainerLookupOptionComponentExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { IListContainerLookupOption } from '../../psServices/models/ps-common-bussiness-interfaces';
import { PsCommonBusinessSettings } from '../../psServices/models/ps-commonBusiness.settings';
import { IOptionsPsOptionScheduledTransferExposed } from '../ps-scheduled-transfers-list/ps-scheduled-transfers-list.component.interface';
import { IOmniTrustedDevices } from './ps-trusted-devices-list.component.interface';


@Component({
    selector: 'ps-trusted-devices-list',
    templateUrl: './ps-trusted-devices-list.component.html'
})
export class PsTrustedDevicesListComponent extends PsTemplateListPage implements OnInit, OnDestroy {
    public updateList: any[];
    public bRefresh = false;
    @Input() options: IOptionsPsOptionScheduledTransferExposed;
    lookUpOptions: IListContainerLookupOption[] = [];
    public labelValuesMap = new Map<string, IMapKeyValue>();
    public headerMap = new Map<string, IMapKeyValue>();

    @Output() reloadFct = new EventEmitter<string>();
    constructor() {
        super();
        this.populateHeaderValueMap();
    }
    ngOnInit() {
        super.init();

        if (this.options && this.options.listOfOptions) {
            this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
            this.options.itemList = this.options.listOfOptions;
        }

    }

    prepareLookUp(card: IOmniTrustedDevices): any {
        const lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
        lookupDetails.labelsValueMap = this.labelValuesMap;
        lookupDetails.headerMap = this.headerMap;
        lookupDetails.formGroup = this.options.group;
        lookupDetails.isEditable = this.options.isEditable;
        lookupDetails.itemList = this.options.itemList;
        lookupDetails.showInitialCardValues = 3;
        lookupDetails.actionDetailsOptions = [];
        if (card.statusCode !== 'B') {
            lookupDetails.actionDetailsOptions.push(
                {
                    id: 'revoke',
                    labelOptions: {
                        labelKey: 'revoke_key'
                    },
                    buttonIcon: 'delete',
                    actionUrl: PsCommonBusinessSettings.serviceUrl.submitDeviceAction,
                    cssClass: 'fab-edit-button',
                    label: 'revoke_key',
                    iconName: 'square-outline',
                    actionType: 'Submit',
                    operId: ConstantCommon.REVOKE_DEVICE_OPER_ID,
                    requestParamKeyVal: { status: 'I'},
                    postCallFunction: {
                        func() {
                            return new Promise<any>((resolve, reject) => {
                                resolve(this.executionClass.reloadList());
                            });
                        },
                        executionClass: this,
                        params: []
                    }
                },
                {
                    id: 'block',
                    labelOptions: {
                        labelKey: 'block_key'
                    },
                    buttonIcon: 'delete',
                    actionUrl: PsCommonBusinessSettings.serviceUrl.submitDeviceAction,
                    cssClass: 'fab-edit-button',
                    label: 'block_key',
                    iconName: 'block',
                    actionType: 'Submit',
                    operId: ConstantCommon.BLOCK_DEVICE_OPER_ID,
                    requestParamKeyVal: { status: 'B'},
                    postCallFunction: {
                        func() {
                            return new Promise<any>((resolve, reject) => {
                                resolve(this.executionClass.reloadList());
                            });
                        },
                        executionClass: this,
                        params: []
                    }
                }
            );
        }
        lookupDetails.reloadFunction = {
            func() {
                return new Promise<any>((resolve, reject) => {
                    resolve(this.executionClass.reloadList());
                });
            },
            executionClass: this,
            params: []
        }

        return lookupDetails;
    }

    reloadList() {
        this.reloadFct.emit();
    }

    private populateHeaderValueMap() {
        let deviceVersionMap: IMapKeyValue = {};
        let devicePlatformMap: IMapKeyValue = {};
        let lastLoginMap: IMapKeyValue = {};
        let headerTitleMap: IMapKeyValue = {};


        deviceVersionMap = {
            key: 'deviceVersion',
            value: '--- --- --- ---',
            isEdit: false,
        };

        lastLoginMap = {
            key: 'deviceLastLogin',
            value: '--- --- --- ---',
            isEdit: false,
        };
        headerTitleMap = {
            key: 'title',
            value: 'title',
            isEdit: false,
            formGroupMap: {
                fcName: 'trustedDeviceTitle',
            }
        };


        this.labelValuesMap.set('device_version_key', deviceVersionMap);
        this.labelValuesMap.set('last_login_key', lastLoginMap);

        this.headerMap.set('title', headerTitleMap);
    }

}
